<template>
  <div>
    <button @click="mailto">Get started</button>
  </div>
</template>

<script>
export default {
  name: 'ContactButton',
  data () {
    return {
    }
  },
  methods: {
    mailto: () => {
      window.location.href = 'mailto:silur@cryptall.co'
    }
  }
}
</script>

<style scoped>
button {
  border-radius: 20px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #663179;
  color: white;
  min-width: 300px;
  min-height: 70px;
  font-weight: bold;
  font-size: 30px;
  margin-top: 50px;
  margin-bottom: 50px;
}
</style>



