<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12">
    <iframe :src="url" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 centered">
        <a :href="url">{{title}}</a>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12">
        {{desc}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MediaCard',
  props: ['title', 'desc', 'url'],
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.magenta {
  color: white;
  background-color: #983a66;
}
a {
  font-weight: bold;
  font-size: 20px;
}
.centered {
  text-align: center;
}
iframe {
  min-height: 250px;
}
.container {
  margin-bottom: 30px;
}
</style>



