<template>
  <div class="container">
    <div class="row rowhead">
      <div class="col-sm-2 rowhead white">
        <div class="img" :style="style"></div>
      </div>
      <div class="col-sm-10 magenta">
        {{ name }}<br><div class="title">{{title}} - <a :href="link">{{company}}</a></div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-2 magenta"></div>
      <div class="col-sm-10 white slot">
        "<slot></slot>"
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Card',
  props: ['img', 'name', 'title', 'link', 'company'],
  mounted () {
    this.imgsrc = require(`@/assets/${this.img}`)
  },
  data () {
    const imgsrc = require(`@/assets/${this.img}`)
    return {
      imgsrc: imgsrc,
      style: `background-image: url('${imgsrc}')`
    }
  }
}
</script>

<style scoped>
.container {
  margin-top: 20px;
}
.white {
  background-color: white;
  color: black;
  font-style: italic;
}
.magenta {
  background-color: #983a66;
  color: white;
}
.rowhead {
  max-height: 4rem;
}
img {
  width: 64px;
  max-height: 100%;
}
.slot {
  padding: 30px;
}
.img {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 64px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;

}
.col-sm-2.white {
  padding: 0;
}
</style>



