<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 img-container">
        <img :src="imgsrc">
      </div>
    </div>
    <div class="row subhead">
      <div class="col-sm-12 col-md-8 col-md-offset-2">
        {{title}}
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 col-lg-11 col-lg-offset-1 slot">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Card',
  props: ['img', 'title'],
  mounted () {
    this.imgsrc = require(`@/assets/${this.img}`)
  },
  data () {
    return {
      imgsrc: require(`@/assets/${this.img}`)
    }
  }
}
</script>

<style scoped>
.container {
  background-color: #f3f3f3;
  margin: 10px;
  min-height: 35rem;
  max-height: 35rem;
  padding-top: 4rem;
  max-width: 35rem;
}
@media screen and (max-width: 1280px) {
  .container {
    min-height: 35.5rem;
    max-height: 35.5rem;
  }
  .slot {
    text-align: left;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    min-height: 35rem;
    max-height: 35rem;
  }
  .slot {
    text-align: left;
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 768px) and (max-width: 930px) {
  .container {
    min-height: 30rem;
    max-height: 30rem;
  }
  .slot {
    text-align: left;
  }
}
.subhead {
  margin-top: 30px;
  margin-bottom: 30px;
  font-weight: bold;
  text-align: left;
}

.subhead > div {
  font-size: 1.4rem
}

.img-container {
  margin-top: 30px;
}
.col-md-8 {
  text-align: center;
}
</style>



