<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 subhead">
        {{title}}
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 col-md-8 col-md-offset-2 subhead-desc">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Card',
  props: ['title'],
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.subhead {
  font-size: 37px;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 30px;
}
.subhead-desc {
  font-size: 17px;
  text-align: left;
  font-weight: bold;
  margin-bottom: 30px;
}
</style>



